.product {
  position: relative;

  &__image {
    position: relative;
    display: flex;
    overflow: hidden;
    margin-bottom: 20px;

    img {
      flex: 0 1 auto;
      max-width: 100%;
      width: 100%;
      max-height: 100%;
      object-fit: cover;
      transition: transform 0.3s ease-in-out;
    }
  }

  &__sale {
    position: absolute;
    left: 20px;
    top: 20px;
    z-index: 20;
    border: 2px solid var(--color-dark);
    padding: 4px 10px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: #111;
  }

  &__prop {
    margin-bottom: 10px;
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-size: 13px;
    line-height: 1.2;
    font-weight: 500;
    color: var(--color-tundora);

    a {
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
      }
    }
  }

  &__price {
    font-weight: 700;
    font-size: 13px;
  }

  &__oldprice {
    margin-left: 5px;
    font-weight: 700;
    font-size: 13px;
    text-decoration: line-through;
    color: #959595;
  }

  &:hover {
    .product__image {
      img {
        transform: scale(1.1);
        transition: transform 0.3s ease-in-out;
      }
    }

    .product-custom__price {
      transform: translateY(-100%);
      transition: transform 0.3s ease-in-out;
    }

    .product-custom__cart {
      transform: translateY(-100%);
      transition: transform 0.3s ease-in-out;
    }
  }
}

.product-prop {
  display: inline-block;
  padding-left: 16px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: #111;
  background-position: left center;
  background-repeat: no-repeat;
}

.product-custom {
  overflow: hidden;
  height: 18px;

  &__price {
    transition: transform 0.3s ease-in-out;
  }

  .to-cart {
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
  }

  &__cart {
    text-transform: uppercase;
    transition: transform 0.3s ease-in-out;
  }
}

.new {
  background-image: url('../img/new.png');
}

.best {
  background-image: url('../img/best.png');
}

.hot {
  background-image: url('../img/hot.png');
}

.top {
  background-image: url('../img/top.png');
}

.only {
  background-image: url('../img/only.png');
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;

  &__item {
    &:not(:last-child) {
      margin-right: 8px;
    }

    svg {
      width: 30px;
      height: 20px;
    }
  }

  &__link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #eee;
    padding: 0 16px;
    height: 40px;
    font-weight: 700;
    font-size: 10px;
    color: var(--color-gray);
    text-transform: uppercase;
    transition: color 0.3s ease-in-out, background 0.3s ease-in-out, border 0.3s ease-in-out;

    svg {
      margin-left: 3px;
      width: 7px;
      height: 16px;
      fill: currentColor;
    }

    &:hover {
      border-color: var(--color-dark);
      color: var(--color-white);
      background-color: var(--color-dark);
      transition: color 0.3s ease-in-out, background 0.3s ease-in-out, border 0.3s ease-in-out;
    }

    &--current {
      border-color: var(--color-dark);
      color: var(--color-white);
      background-color: var(--color-dark);
    }
  }
}



