.main-products {
  &__container {
    padding-top: 55px;
    padding-bottom: 55px;

    @include md {
      padding-bottom: 0;
    }
  }

  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 46px;
  }

  &__item {
    margin: 0 18px;

    @include xs {
      margin: 0 6px;
    }
  }

  &__btn-center {
    text-align: center;
  }

  &__more {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    margin-top: 95px;
    font-weight: 500;
    font-size: 13px;
    color: var(--color-gray);

    span {
      display: inline-block;
      margin-bottom: 12px;
    }

    svg {
      width: 12px;
      height: 21px;
      fill: var(--color-dark);
      transition: transform 0.3s;
    }

    &:hover {
      svg {
        transform: translateY(5px);
        transition: transform 0.3s;
      }
    }

    @include md {
      margin-top: 0;
    }
  }

  &__btn {
    font-size: 24px;

    &--current {
      &::before {
        opacity: 1;
        transition: opacity 0.15s;
      }
    }

    @include sm {
      font-size: 20px;
    }

    @include xs {
      font-size: 14px;
    }
  }
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 80px 30px;

  @include md {
    gap: 50px 30px;
  }

  @include xs {
    gap: 30px 30px;
  }

  &__item {
    width: calc((100% - 30px * 4) / 5);

    @include md {
      width: calc((100% - 30px * 2) / 3);
    }

    @include xs {
      width: calc((100% - 30px) / 2);
    }

    &:nth-child(n+6) {
      display: none;
    }

    &--visible {
      display: block !important;
    }
  }
}


