.hero-catalog {
  padding-top: 130px;

  .hero-pag {
    .swiper-pagination-bullet {
      background-color: var(--color-tundora);
    }
  }

  &__slider {
    position: relative;
    overflow: hidden;
    width: 100%;
    background-color: #f8f8f8;
  }

  &__container {
    position: relative;
  }

  @include xs {
    padding-top: 70px;
  }
}

.catalog-slide {
  padding: 108px;

  @include sm {
    padding: 70px;
  }

  @include xs {
    padding: 20px;
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    max-width: 375px;
    font-weight: 700;
    font-size: 36px;

    @include mobile {
      font-size: 30px;
      max-width: 350px;
    }

    @include xs {
      font-size: 26px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    max-width: 390px;
    font-size: 15px;
    line-height: 28px;
    color: var(--color-tundora);

    @include mobile {
      max-width: 320px;
    }

    @include xs {
      margin-bottom: 30px;
      font-size: 12px;
      line-height: 16px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;

    @include xs {
      display: block;
      padding-bottom: 70px;
    }
  }

  &__image {
    position: absolute;
    right: 19%;
    bottom: 0;

    @include mobile {
      right: 7%;
      height: 445px;
    }

    @include sm {
      display: none;
    }
  }

  &__link {
    display: inline-block;
    padding: 16px 43px;
    font-size: 13px;

    @include xs {
      padding: 12px 30px;
      font-size: 12px;
    }

    &:not(:last-child) {
      margin-right: 15px;
    }

    &--dark {
      border: 1px solid var(--color-dark);
      color: var(--color-white);
      background-color: var(--color-dark);
      transition: all 0.3s ease-in-out;

      &:hover {
        border: 1px solid var(--color-dark);
        color: var(--color-dark);
        background-color: transparent;
        transition: all 0.3s ease-in-out;
      }
    }

    &--light {
      border: 1px solid var(--color-dark);
      color: var(--color-dark);
      background-color: transparent;
      transition: all 0.3s ease-in-out;

      &:hover {
        border: 1px solid var(--color-dark);
        color: var(--color-white);
        background-color: var(--color-dark);
        transition: all 0.3s ease-in-out;
      }

      @include xs {
        margin-top: 20px;
      }
    }
  }
}

.hero-prev-btn,
.hero-next-btn {
  position: absolute;
  left: 30px;
  top: 50%;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-dark);
  width: 48px;
  height: 48px;
  color: var(--color-dark);
  transform: translateY(-50%);
  transition: background 0.3s ease-in-out;

  @include sm {
    left: 10px;
  }

  @include xs {
    display: none;
  }

  svg {
    width: 40px;
    height: 20px;
    fill: currentColor;
    transition: fill 0.3s ease-in-out;
  }

  &:hover {
    background-color: var(--color-dark);
    transition: background 0.3s ease-in-out;

    svg {
      fill: var(--color-white);
      transition: fill 0.3s ease-in-out;
    }
  }
}

.hero-next-btn {
  left: auto;
  right: 30px;

  @include sm {
    right: 10px;
  }
}
